<template>
  <div class="title">
    <span class="anchor">|| </span>
    <span>Course数据表数据</span>
  </div>
  <el-table :data="course" stripe style="width: 100%">
    <el-table-column prop="course_id" label="课程id" width="190" />
    <el-table-column prop="course_name" label="课程名称" />
    <el-table-column prop="course_description" label="课程简介" />
    <el-table-column prop="course_cover" label="封面图" width="160" />
    <el-table-column prop="course_type" label="类别" width="80" />
  </el-table>
  <div class="title">
    <span class="anchor">|| </span>
    <span>CourseItem数据表数据</span>
  </div>
  <el-table :data="courseItem" stripe style="width: 100%">
    <el-table-column prop="course_id" label="课程id" width="190" />
    <el-table-column prop="course_item" label="课程引导" />
    <el-table-column prop="course_result" label="检查点" width="80" />
  </el-table>
  <div class="title">
    <span class="anchor">|| </span>
    <span>Assignment数据表数据</span>
  </div>
  <el-table :data="assignment" stripe style="width: 100%">
    <el-table-column prop="assignment_id" label="练习id" width="190" />
    <el-table-column prop="assignment_name" label="练习名称" />
    <el-table-column prop="assignment_description" label="练习简介" />
    <el-table-column prop="assignment_cover" label="封面图" width="160" />
    <el-table-column prop="assignment_type" label="类别" width="80" />
  </el-table>
  <div class="title">
    <span class="anchor">|| </span>
    <span>AssignmentItem数据表数据</span>
  </div>
  <el-table :data="assignmentItem" stripe style="width: 100%">
    <el-table-column prop="assignment_id" label="练习id" width="190" />
    <el-table-column prop="assignment_item" label="题目详情" />
    <el-table-column prop="assignment_result" label="正确答案" width="80" />
  </el-table>
</template>

<script>
import axios from "axios";

export default {
  name: "App",
  data() {
    return {
      assignment: [],
      assignmentItem: [],
      course: [],
      courseItem: [],
    };
  },
  mounted() {
    axios.get("https://lyrbbding.site/lyr/assignment").then((res) => {
      this.assignment = res.data.res;
    });
    axios.get("https://lyrbbding.site/lyr/assignment_item").then((res) => {
      this.assignmentItem = res.data.res;
    });
    axios.get("https://lyrbbding.site/lyr/course").then((res) => {
      this.course = res.data.res;
    });
    axios.get("https://lyrbbding.site/lyr/course_item").then((res) => {
      this.courseItem = res.data.res;
    });
  },
};
</script>

